/* src/app/pages/login/login.css */
:host {
  display: block;
  min-height: calc(100vh - 60px);
  background:
    linear-gradient(
      135deg,
      #324260 0%,
      #6C8DB5 100%);
  position: relative;
  overflow: hidden;
}
:host::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image:
    radial-gradient(
      circle at 20% 50%,
      rgba(255, 255, 255, 0.1) 0%,
      transparent 50%),
    radial-gradient(
      circle at 80% 80%,
      rgba(255, 255, 255, 0.1) 0%,
      transparent 50%);
  animation: float 20s ease-in-out infinite;
}
.container {
  position: relative;
  z-index: 1;
}
.card {
  border: 1px solid var(--border);
  border-radius: var(--radius-2xl);
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  background: rgba(255, 255, 255, 0.98);
  box-shadow: var(--shadow-2xl);
  overflow: hidden;
  animation: fadeInUp 0.6s ease-out;
}
.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: var(--accent);
}
.card-body {
  position: relative;
}
.text-center h2 {
  background:
    linear-gradient(
      135deg,
      var(--primary) 0%,
      var(--accent) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 900;
  margin-bottom: 0.5rem;
  animation: slideDown 0.6s ease-out 0.2s both;
}
.text-muted {
  color: var(--text-secondary) !important;
  animation: fadeIn 0.6s ease-out 0.4s both;
}
.form-label {
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.form-label i {
  color: var(--accent);
}
.form-control {
  border: 2px solid var(--border);
  border-radius: var(--radius-lg);
  padding: 0.75rem 1rem;
  transition: all var(--transition-base);
  background: var(--white);
  color: var(--text-primary);
}
.form-control:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 0.25rem rgba(108, 141, 181, 0.15);
  transform: translateY(-2px);
}
.form-control::placeholder {
  color: var(--text-secondary);
}
.invalid-feedback {
  display: block;
  margin-top: 0.5rem;
  color: var(--danger);
  font-size: 0.875rem;
  font-weight: 500;
  animation: shake 0.3s ease-in-out;
}
.form-control.is-invalid {
  border-color: var(--danger);
  animation: shake 0.3s ease-in-out;
}
a[routerLink="/forgot-password"] {
  color: var(--accent);
  text-decoration: none;
  font-weight: 600;
  transition: all var(--transition-base);
  position: relative;
}
a[routerLink="/forgot-password"]::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--accent);
  transition: width var(--transition-base);
}
a[routerLink="/forgot-password"]:hover {
  color: var(--accent-dark);
}
a[routerLink="/forgot-password"]:hover::after {
  width: 100%;
}
.btn-primary {
  padding: 0.875rem 2rem;
  font-weight: 700;
  font-size: 1.05rem;
  border-radius: var(--radius-lg);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
  background: var(--accent);
  border: none;
}
.btn-primary::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}
.btn-primary:hover {
  background: var(--accent-dark);
}
.btn-primary:hover::before {
  width: 300px;
  height: 300px;
}
.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background: var(--accent);
}
.text-center a[routerLink="/register"] {
  color: var(--accent);
  font-weight: 700;
  text-decoration: none;
  position: relative;
  transition: all var(--transition-base);
}
.text-center a[routerLink="/register"]::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--accent);
  transform: scaleX(0);
  transition: transform var(--transition-base);
}
.text-center a[routerLink="/register"]:hover {
  color: var(--accent-dark);
}
.text-center a[routerLink="/register"]:hover::after {
  transform: scaleX(1);
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-5px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(5px);
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.spinner-border-sm {
  animation: spin 1s linear infinite;
}
@media (max-width: 576px) {
  .card {
    border-radius: var(--radius-xl);
  }
  .btn-primary {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
  }
}
/*# sourceMappingURL=login.css.map */
